import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-breakfast',
  templateUrl: './breakfast.page.html',
  styleUrls: ['./breakfast.page.scss'],
})
export class BreakfastPage implements OnInit {
  profile;
  nome;
  imgOne = 'assets/images/dieta.jpg'

  slideOpts2 = {
    slidesPerView: 3.9,
  };

  slideOpts1 = {
    slidesPerView: 1.3,
  };


  constructor(
    private router  : Router,
    private navctr  : NavController,
    private authSrv : AuthService
  ) { }

  ngOnInit() {
    this.loadProfile()
  }

  onRecipes(){
    this.router.navigate(['recipes']);
  }

  loadProfile(){
    this.authSrv.profile().subscribe(data => {
      this.profile = data
      this.nome = data.nome
        console.log(this.profile)
    })
    
  }

  onClick(){

  }
  onBack (){
    this.navctr.back();
  }
  goToDieta(){
    this.router.navigate(['menu-diet'])
  }

  goToSleep(){
    this.router.navigate(['sleep-schedule'])
  }
  goToWorkout(){
    this.router.navigate(['menu-exercicios'])
  }
  goToExams(){
    this.router.navigate(['menu-exames'])
  }
  goToHidratar(){
    this.router.navigate(['tabs/tab2'])
  }
  

}
